@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-padding-top: 56px;
  scroll-behavior: smooth;
}

/*
* -----------------------------------------------------------
*                       FONTS STACK
* -----------------------------------------------------------
*/
@font-face {
  font-family: 'Calibre';
  src: local('Calibre-Bold'),
      url('../fonts/Calibre/Calibre-Bold.woff2') format('woff2'),
      url('../fonts/Calibre/Calibre-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Calibre';
  src: local('Calibre-Semibold'),
      url('../fonts/Calibre/Calibre-Semibold.woff2') format('woff2'),
      url('../fonts/Calibre/Calibre-Semibold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Calibre';
  src: local('Calibre-Medium'),
      url('../fonts/Calibre/Calibre-Medium.woff2') format('woff2'),
      url('../fonts/Calibre/Calibre-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Calibre';
  src: local('Calibre'),
      url('../fonts/Calibre/Calibre.woff2') format('woff2'),
      url('../fonts/Calibre/Calibre.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/*
* -----------------------------------------------------------
*                     STANDARD STYLES
* -----------------------------------------------------------
*/
code, kbd, pre, samp {
  @apply text-base font-mono;
}

::selection {
  @apply bg-blue-100 text-blue-900;
}

/*
* -----------------------------------------------------------
*                         APP SIDEBAR
* -----------------------------------------------------------
*
*  Animations with tailwind classes are bit too much. The
*  simplest option is to self assign these properties.
*
*/
.sidebar {
  transition: transform 200ms ease;
  transform: translateX(-100%);
}

.sidebar.opened {
  transform: translateX(0);
}

.sidebar-overlay {
  opacity: 0;
  pointer-events: none;
  transition: opacity 150ms ease 100ms;
}

.sidebar-overlay.opened {
  opacity: 0.5;
  pointer-events: auto;
}

@media (min-width: 1024px) {
  .sidebar {
    transition: none;
    transform: none;
  }
}

/*
* -----------------------------------------------------------
*                         DOC TOC
* -----------------------------------------------------------
*
*  Cannot be styled using Tailwind since HTML is not under
*  out control.
*
*/
.toc-container li {
  padding-bottom: 4px;
}

.toc-container div > li {
  @apply list-none;
}

.toc-container div > li > p {
  @apply hidden;
}

.toc-container div > li > ul > li {
  padding-top: 7px;
  padding-bottom: 7px;
  border-bottom: 1px dashed theme('colors.gray.300');
}

.toc-container div > li > ul li ul {
  margin-left: 10px;
  margin-top: 5px;
}

.toc-container div li {
  line-height: 1.2;
}

.toc-container div li a:hover, .toc-container div li a.active {
  @apply text-gray-800;
}

/*
* -----------------------------------------------------------
*                     ALGOLIA SEARCH
* -----------------------------------------------------------
*
* Again HTML is out of our control and hence needs to be
* styled within css using selectors
*
*/
.DocSearch {
  --docsearch-highlight-color: theme('colors.brand.DEFAULT');
  --docsearch-searchbox-shadow: inset 0 0 0 2px theme('colors.brand.DEFAULT');
  --docsearch-primary-color: theme('colors.brand.DEFAULT');
  --docsearch-text-color: theme('colors.gray.800');
  --docsearch-muted-color: theme('colors.purple.600');
  --docsearch-modal-background: theme('colors.gray.100');
  --docsearch-searchbox-background: #4533cc;
  --docsearch-key-gradient: theme('colors.gray.200');
  --docsearch-key-shadow: '';
  --docsearch-modal-shadow: theme('boxShadow.dropdown');
}

.DocSearch.DocSearch-Button {
  width: 100%;
  display: flex;
  border-radius: 4px;
  margin: 0;
  color: #b7b0e3;
  background: transparent;
  position: relative;
}

.DocSearch.DocSearch-Button:hover {
  background: #4533cc;
  color: #fff;
}

@media (min-width: 751px) {
  .DocSearch.DocSearch-Button {
    background: var(--docsearch-searchbox-background);
  }
}

.DocSearch-Button-Placeholder {
  flex: 1;
  text-align: left;
}

.DocSearch .DocSearch-Button-Placeholder {
  font-size: 1.125rem;
}

.DocSearch.DocSearch-Button .DocSearch-Button-Key {
  background: none;
  width: auto;
  padding: 0;
  color: #b7b0e3;
}

.DocSearch.DocSearch-Button:hover .DocSearch-Button-Key {
  color: #fff;
}

.DocSearch.DocSearch-Button span:nth-child(3) {
  font-size: 11px;
  top: 0;
}

.DocSearch.DocSearch-Button span:nth-child(4) {
  font-size: 18px;
}

.DocSearch.DocSearch-Button:active, .DocSearch.DocSearch-Button:focus, .DocSearch.DocSearch-Button:hover {
  box-shadow: none;
}

.DocSearch.DocSearch-Button .DocSearch-Search-Icon {
  color: #fff;
  width: 18px;
  margin: 0 5px 0 9px;
}
@media (min-width: 751px) {
  .DocSearch.DocSearch-Button .DocSearch-Search-Icon {
    color: inherit;
  }
}

/*
* -----------------------------------------------------------
*                     MARKDOWN STYLES
* -----------------------------------------------------------
*
*  Styling elements generated by converting markdown to HTML
*
*/
.markdown-content {
  word-wrap: break-word;
  overflow-wrap: break-word;
  line-height: 1.4;
}

.markdown-content code {
  @apply text-gray-700 font-medium bg-gray-200;
  font-size: 0.875em;
  border-radius: 3px;
  padding: 0 4px;
}

.markdown-content strong {
  @apply font-semibold text-gray-700;
}

.markdown-content pre code {
  background-color: transparent;
  border-width: 0;
  border-radius: 0;
  padding: 0;
  font-weight: 400;
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

.markdown-content pre {
  white-space: pre;
  overflow-x: auto;
}

.markdown-content pre .line {
  min-height: 18px;
}

.markdown-content h1, .markdown-content h2, .markdown-content h3, .markdown-content h4 {
  @apply text-gray-900 font-display font-semibold;
}

.markdown-content h1 {
  @apply text-5xl font-bold mb-7;
}

.markdown-content p, .markdown-content .alert, .markdown-content .embed, .markdown-content img, .markdown-content pre {
  @apply mb-7;
}

.markdown-content h2 {
  @apply mt-14 pt-12 mb-6;
  font-size: 1.9rem;
  border-top: .1rem solid theme('colors.gray.400');
}

.markdown-content ul, .markdown-content ol {
  @apply my-8 ml-4;
}

.markdown-content li {
  @apply pl-5 relative mb-1.5;
}

.markdown-content ul li:before {
  @apply top-0 left-0 block absolute;
  content: "•";
}

.markdown-content a {
  @apply text-brand;
  text-decoration: none;
  background-image: linear-gradient(to bottom, transparent 50%, theme('colors.brand.DEFAULT') 50%);
  background-position: 0 93%;
  background-repeat: repeat-x;
  background-size: 100% .15rem;

  text-shadow: 0.1rem 0 theme('colors.gray.100'),
    0.15rem 0 theme('colors.gray.100'),
    -0.1rem 0 theme('colors.gray.100'),
    -0.15rem 0 theme('colors.gray.100');
}

